import React, { Component } from 'react';
import { NavBar, Icon, } from 'antd-mobile';
import Content from '../../views/common/Content';

import '../../assets/css/traffic.css';

export default class TrafficLocalScreen extends Component
{
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}>
                    本地交通
                </NavBar>
                <Content style={{backgroundColor: '#efeff4'}}>
                    <div className="trafficDetailCard">
                        <div className="trafficDetailContent">
                            <div className="trafficDetailTitle1">新县公交</div>

                            <div className="trafficDetailTitle2">公交介绍</div>
                            <div className="trafficDetailLongText">
                                2014年10月15日新县人民政府第十三次常务会议通过资产评估的资产价格和达成的空闲土地收购价格一次性收购县公交公司。11月初，原“新县公交交通有限公司”正式被政府收购，由一个私营企业转变成为隶属于新县交通局的独资企业。自此，新县的城市公共交通迈入了一个全新的发展历程。
                            </div>

                            <div className="trafficDetailTitle2">未来规划</div>
                            <div className="trafficDetailLongText">
                                新县现有的公交站点，普遍建于十多年前，都只有路牌条件比较差，遇到雨雪天气，会给乘客带来不方便，但是，修建公交站点也不是简单的事，要面临原有道路的宽窄，考虑是否遮挡街头门店等问题，所以，目前只对部分站点进行了规划设计。根据现有城市公共交通服务存在的问题和不足，有针对性的制定出新的规划方案，其中，对当前的公交站点进行了新的设计规划，将建成部分港湾式站点进而改善乘客候车环境，这项工作正在由城乡建设局负责设计，公用事业局进行建设，预计2015年落实完成。
                            </div>
                            <div className="trafficDetailLongText">
                                全力落实老年人、残疾人免费乘车政策，这也是县政府着力要解决的问题，公司目前正在加快IC卡语音识别系统升级和老年人、残疾人人身意外保险的办理工作，公司上下正全力客服困难，加速这项工作的尽快落实。将在2015年全面推行，六十周岁以上老人和残疾人免费，乘坐公交车等一系列的政策，我们公交公司积极的响应县委县政府的号召，把这项惠民政策落到实处，我们将在车辆投入上、车辆调度上、司机招聘上能够更加满足广大人民群众出行的需求，同时我们也面临着一些困难，IC卡系统的升级问题。我们正在于IC卡系统的发行单位进行联系,尽快的将IC卡的系统升级，适合于老年人、残疾人乘车的语音提示，第二个就是在意外伤后保险上我们主动的与政府部门进行沟通和协商，要求所有的乘车者（人员）必须要按照规定购买意外伤后保险，减轻我们企业的责任和负担。
                            </div>
                            <div className="trafficDetailLongText">
                                新县城市公共交通有限公司，虽然才刚刚组建一个多月，但在县委县政府的大力支持和全体公交员工的共同努力下，已经萌发出勃勃生机，随着公司整体工作的不断推进和规划设计的逐项落实，相信要不了多久一个基础设施完善、服务功能齐全、安全、畅通、文明、和谐的公共交通环境就会呈现于世人面前。独资企业性质的公交运营模式是新县城市功能定位的发展提升，进入到新阶段的必然产物，它也必将在新县的社会经济发展中发挥新的、积极的作用。
                            </div>

                            <div className="trafficDetailTitle2">新县公交集团</div>
                            <img src={require('../../assets/images/traffic/local.jpg')} alt='' className="trafficDetailImage" style={{marginTop: '20px'}}/>
                            <div className="trafficDetailLongText">
                            新县城市公交车最早出现于1999年，十几年来它为新县广大市民的出行带来方便和便捷，为新县的发展发挥了积极作用，然而，随着新县社会经济的快速发展，原有的城市公交服务系统很多方面条件已经明显滞后，面临公司运营困难，服务质量难以提高，配套设施无力投资的困境。
                                                新成立的新县城市公共交通有限公司目前已经顺利实现平稳过渡，办公地点依旧设在新县客运站，公司现有驾驶员、后勤人员共48人。目前拥有运行车辆28台，分布在四条公交线路，公司在保持正常运营秩序的基础上，迅速推出了一系列改进服务新措施。首先，加强内部管理，健全和完善各项规章制度；制定了新县城市交通有限公司行业服务机制，对公交服务进行规范管理，树立“友善待乘客、互敬待同行、车厢送新风、公交为民众”的行业新风。强化人员业务素质培训，招聘技术过硬驾驶人员，提高服务水平，制定受理旅客投诉管理制度，凡是乘客投诉无论何种形式，如信访、来人上访、来电话、上级下传、媒体等。
                                                均予以受理，严整公司纪律，加强驾驶员的工作责任心和安全意识，对驾驶员开车时打电话、抽烟等违规问题进行相应处罚，规定了新的驾驶员“十要十不要”服务方针，严格做到文明礼貌服务，始终把乘客放在首位。城市公交车是一道流动的风景线，是广大市民重要的出行工具，更是展示城市形象和市民素质的重要窗口。
                            </div>

                            <div className="trafficDetailTitle1">新县出租车</div>
                            <div className="trafficDetailTitle2">出租车介绍</div>
                            <div className="trafficDetailLongText">
                                新县出租汽车公司隶属信阳市弘运运输集团新县分公司属下。新县政府及市有关管理部门批准，成立于2005年，现有出租汽车61台，承载着新县广大城区市民另时出行的运输业务。
                            </div>

                            <div className="trafficDetailTitle2">经营宗旨</div>
                            <img src={require('../../assets/images/traffic/local-1.jpg')} alt='' className="trafficDetailImage" style={{marginTop: '20px'}}/>
                            <div className="trafficDetailLongText">
                                公司的经营宗旨是，严格按照中华人民共和国建设部、公安部出台的《城市出租汽车管理办法》、《中华人民共和国道路安全法》、《中华人民共和国道路运输条例》、新县人民政府办公室印发的《关于整治新县客运市场秩序专项行动实施方案的通知》等有关法律法规执行，以人为本，以安全健康的模式发展。
                            </div>

                            <div className="trafficDetailTitle2">大力提倡</div>
                            <div className="trafficDetailLongText">
                                在本公司大力提倡“争做文明驾驶人，为和谐社会作贡献”。我们的倡议是：
                            </div>
                            <div className="trafficDetailLongText">
                                一、大力倡导“六大文明驾驶行为”。1大力倡导自觉遵守《交通法规》，文明开车；2、大力倡导不超员、不超速；3、大力倡导不乱停车乱放车辆；4、大力倡导喝酒不开车、开车不喝酒的良好习惯；5、大力倡导文明会用车灯，不开赌气车的良好品德；6、大力倡导文明驾驶，遵守交通信号指挥等文明行为。
                            </div>
                            <div className="trafficDetailLongText">
                                二、自觉摒弃“溜达驾驶车辆陋习”。1、自觉摒弃车辆随意变更车道；2、自觉摒弃占用应急车道；3、自觉摒弃开车接打手机；4、自觉摒弃开车不系安全带；5、自觉摒弃驾驶车辆时与乘客闲聊；6、自觉摒弃驾驶车辆时穿短裤、拖鞋、衣帽不整、吸烟等陋习。
                            </div>
                            <div className="trafficDetailLongText">
                                提高广大驾驶员素质，增强诚信经营意识，积极为和谐社会作贡献。近年来，我公司驾驶员每逢高考期间，自发组织爱心车队免费接送高考师生，自购矿泉水免费提供考生饮用。深受广大考生和老师称赞，得到社会认可，多次受上级有关单位的表扬。
                            </div>

                            <div className="trafficDetailTitle2">对乘客的承诺</div>
                            <div className="trafficDetailLongText">
                                诚信经营，承诺社会。为营造一个和谐的出租汽车经营环境，打造公司品牌，树立良好的服务形象，我公司于8月13日借广播电视为品台，向新县广大乘客作承诺：
                            </div>
                            <div className="trafficDetailLongText">
                                一、以下地址实行打表计费：白天起步价3元，含1.5公里，超出里程每公里1.67元；夜晚21点后起步4元，含1.5公里，超出里程每公里1.67元。打表计费范围：北至方湾、交通加油站、东至代咀洞口、三中，南至大广红绿灯、羚锐制药厂；
                            </div>
                            <div className="trafficDetailLongText">
                                二、超出以上地址，长、短途采用乘客与驾驶员协议价；
                            </div>
                            <div className="trafficDetailLongText">
                                三、文明待客、优质服务；
                            </div>
                            <div className="trafficDetailLongText">
                                四、文明行车、规范经营、不欺客、不宰客、不强行拼客、不拒载； 四、文明行车、规范经营、不欺客、不宰客、不强行拼客、不拒载；
                            </div>
                            <div className="trafficDetailLongText">
                                五、叫车电话：0376-2966001；
                            </div>
                            <div className="trafficDetailLongText">
                                六、您在乘坐本公司出租汽车时，如有该车驾驶员违反本承诺，请您及时投诉。投诉电话：13839715977、0376-2980558，我们一定将处理结果反馈给您，欢迎您的监督。
                            </div>

                            <div className="trafficDetailTitle1">信阳市弘运运输集团</div>
                            <div className="trafficDetailTitle2">新县分公司简介</div>
                            <img src={require('../../assets/images/traffic/local-2.jpg')} alt='' className="trafficDetailImage" style={{marginTop: '20px'}}/>
                            <img src={require('../../assets/images/traffic/local.jpg')} alt='' className="trafficDetailImage"/>
                            <div className="trafficDetailLongText">
                                一、公司现有大、中、小型豪华客车40余台，从业人员100余名，服务热线：0376-3277396、0376-3277398、0376-3277399。
                            </div>
                            <div className="trafficDetailLongText">
                                二、公司下属出租汽车公司，目前拥有出租车61台，服务热线：0376-2966001。
                            </div>
                            <div className="trafficDetailLongText">
                                （1）、出租车收费标准：起步价3元，含1.5公里，出租车旅游包车价格面议。
                            </div>
                            <div className="trafficDetailLongText">
                                （2）、附县内旅游景点出租车价格一览表（仅供参考）。
                            </div>
                            <table border="1px" className="trafficDetailTable">
                                <tbody>
                                    <tr>
                                        <th style={{width: '60%'}}>景区地点</th>
                                        <th>价格</th>
                                    </tr>
                                    <tr>
                                        <td>田铺将军墓</td>
                                        <td>120元</td>
                                    </tr>
                                    <tr>
                                        <td>田铺大湾</td>
                                        <td>100元</td>
                                    </tr>
                                    <tr>
                                        <td>八里丁李湾</td>
                                        <td>80元</td>
                                    </tr>
                                    <tr>
                                        <td>周河西河湾</td>
                                        <td>80元</td>
                                    </tr>
                                    <tr>
                                        <td>周河毛铺古村居</td>
                                        <td>80元</td>
                                    </tr>
                                    <tr>
                                        <td>金兰山</td>
                                        <td>120元</td>
                                    </tr>
                                    <tr>
                                        <td>九龙潭</td>
                                        <td>30元</td>
                                    </tr>
                                    <tr>
                                        <td>江淮岭</td>
                                        <td>30元</td>
                                    </tr>
                                    <tr>
                                        <td>大别山干部学院</td>
                                        <td>20元</td>
                                    </tr>
                                    <tr>
                                        <td>香山湖</td>
                                        <td>30元</td>
                                    </tr>
                                    <tr>
                                        <td>中华园</td>
                                        <td>20元</td>
                                    </tr>
                                    <tr>
                                        <td>泗店将军石</td>
                                        <td>60元</td>
                                    </tr>
                                    <tr>
                                        <td>陈店李德生故居</td>
                                        <td>60元</td>
                                    </tr>
                                    <tr>
                                        <td>千斤杨高山水库</td>
                                        <td>120元</td>
                                    </tr>
                                    <tr>
                                        <td>浒湾游泳池</td>
                                        <td>50元</td>
                                    </tr>
                                    <tr>
                                        <td>箭河街</td>
                                        <td>40元</td>
                                    </tr>
                                    <tr>
                                        <td>郭家河莲花堰</td>
                                        <td>120元</td>
                                    </tr>
                                    <tr>
                                        <td>吴陈河阳土墩</td>
                                        <td>80元</td>
                                    </tr>
                                </tbody>
                            </table>
                            <div className="trafficDetailLongText">
                                公司秉承“安全、经济、方便、快捷”为宗旨，出租车以一流的形象服务新县、扎根新县，作为服务窗口的企业，公司郑重承诺，为蓝天红城新县添光增彩，为新县红色旅游尽职尽责。
                            </div>
                            <div className="trafficDetailLongText">
                                公司地址：新县发展大道与朝阳路交汇处 信阳市弘运运输集团有限公司新县分公司
                            </div>
                            <div className="trafficDetailLongText">
                                日 期：2016年11月28日
                            </div>
                        </div>
                    </div>
                </Content>
            </div>
        );
    }
}


